
.num-count {
  font-size: 0;
}
.num-count .icon-remove_circle_outline {
  display: inline-block;
  vertical-align: bottom;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.num-count .count {
  display: inline-block;
  vertical-align: bottom;
  font-size: 10px;
  line-height: 24px;
  color: #93999f;
  margin-left: 12px;
}
.num-count .icon-add_circle {
  display: inline-block;
  vertical-align: bottom;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  margin-left: 12px;
}


