
.ratingselect {
  padding: 18px 18px 0px 18px;
  font-size: 0;
  position: relative;
}
.ratingselect:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  content: ' ';
  border-top: 1px solid rgba(7,17,27,0.2);
}
.ratingselect .button-group {
  padding-bottom: 18px;
  position: relative;
}
.ratingselect .button-group:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  content: ' ';
  border-top: 1px solid rgba(7,17,27,0.2);
}
.ratingselect .button-group .total {
  display: inline-block;
  font-size: 12px;
  color: #fff;
  line-height: 16px;
  padding: 8px 12px;
  background-color: rgba(0,160,220,0.2);
  border: none;
  border-radius: 2px;
}
.ratingselect .button-group .total.active {
  background-color: #00a0dc;
}
.ratingselect .button-group .total .total-num {
  font-size: 8px;
}
.ratingselect .button-group .satisfy {
  display: inline-block;
  font-size: 12px;
  color: #fff;
  line-height: 16px;
  padding: 8px 12px;
  margin-left: 8px;
  background-color: rgba(0,160,220,0.2);
  border: none;
  border-radius: 2px;
}
.ratingselect .button-group .satisfy.active {
  background-color: #00a0dc;
}
.ratingselect .button-group .satisfy .satisfy-num {
  font-size: 8px;
}
.ratingselect .button-group .unsatisfy {
  display: inline-block;
  font-size: 12px;
  color: #fff;
  line-height: 16px;
  padding: 8px 12px;
  margin-left: 8px;
  background-color: rgba(77,85,93,0.2);
  border: none;
  border-radius: 2px;
}
.ratingselect .button-group .unsatisfy.active {
  background-color: #4d555d;
}
.ratingselect .button-group .unsatisfy .unsatisfy-num {
  font-size: 8px;
}
.ratingselect .switch {
  padding: 12px 0;
  font-size: 0;
}
.ratingselect .switch .icon {
  display: inline-block;
  margin-right: 4px;
  line-height: 24px;
  font-size: 24px;
  color: #93999f;
  vertical-align: middle;
}
.ratingselect .switch .icon.onlycon {
  color: #00a0dc;
}
.ratingselect .switch .text {
  display: inline-block;
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
  vertical-align: middle;
}


