
.split {
  width: 100%;
  height: 16px;
  background-color: #f3f5f7;
  position: relative;
}
.split:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  content: ' ';
  border-top: 1px solid rgba(7,17,27,0.2);
}


